/**************

        Index

    I - Size Manipulation
   II - Image Manipulation
  III - Container Manipulation
   IV - ScrollBar Manipulation
    V - Badge
   VI - Footer Manipulation

*****************/
    body {
        margin:0;
        font-family:'Poppins', sans-serif;
    }


/***********************************
 
    I - Size Manipulation

***********************************/
    
    .h-full{
        height:100vh;
    }

    .h-min-500{
        min-height: 500px;
    }

    .h-min-400{
        min-height:400px;
    }
    
    .h-20{
        height:20px;
    }

    .w-20{
        width:20px !important;
    }
    

/***********************************

    II - Image Manipulation

***********************************/

    .image{
      
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;

    }

/***********************************

    III - Container Manipulation

***********************************/
    
    .card-container{
        display:flex;
        flex-wrap:nowrap;
        gap:20px;
        overflow-x:auto;
    }
   
/***********************************

    IV - ScrollBar Manipulation

***********************************/

    ::-webkit-scrollbar {
        width: 12px;
    }
    
    ::-webkit-scrollbar-thumb {
        background: #ef4444;
        border-radius: 20px;
    }

/***********************************

    V - Badge

***********************************/    
    
    .card-badge{
        position:absolute;
        top:8px;
        left:12px;
        padding: 5px 7px;
        border-radius: 2px;
        font-weight: bold;
    }

/***********************************

    VI - Footer Manipulation

***********************************/  

    .footer{
        background: #ef4444;
    }

/***********************************/